@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600&display=swap);
body{font-family:"Raleway",sans-serif;font-weight:400;color:#202020;-webkit-font-smoothing:antialiased;font-style:normal;background-color:#f4f4f4;margin:0;line-height:1.2;font-size:16}p,ul,ol,dd{margin:0 0 1em}ul,ol,dl{padding:0 0 0 1.75em}h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6{font-weight:600;margin:0 0 .75em}h1,.h1{font-size:24px}@media (min-width: 576px){h1,.h1{font-size:32px}}h2,.h2{font-size:20px}@media (min-width: 576px){h2,.h2{font-size:22px}}.container{max-width:900px;padding:0 15px;margin:0 auto;background:#fff;box-sizing:border-box}@media (min-width: 576px){.container{padding:0 30px}}.track{padding:1.5em 0;box-sizing:border-box;border-bottom:1px solid #ebebeb}@media (min-width: 576px){.track{display:flex;justify-content:space-between;align-items:center}}.track .track__thumb{margin-bottom:1.5em}@media (min-width: 576px){.track .track__thumb{flex:0 0 300px;margin-bottom:0}}.track .track__thumb .track__video{width:100%;height:0;padding-top:56.25%;position:relative;overflow:hidden}.track .track__thumb .track__video iframe{position:absolute;top:0;left:0;width:100%;height:100%;border:0}@media (min-width: 576px){.track .track__info{padding-left:2em;flex:auto}}.track .track__info .track__user-info{display:flex;align-items:center;flex-wrap:wrap;text-decoration:none;color:#202020}.track .track__info .track__user-info .track__user-avatar{width:45px;height:45px;border-radius:100%;border:1px solid #f4f4f4;overflow:hidden;display:block;background-size:cover;background-repeat:no-repeat;transition:all .2s ease-in-out}.track .track__info .track__user-info .track__username{border-bottom:1px solid transparent;transition:all .2s ease-in-out;display:block;margin-left:15px}.track .track__info .track__user-info:hover .track__user-avatar{transform:scale(1.05)}.track .track__info .track__user-info:hover .track__username{color:#000;border-bottom:1px solid}

